.file-list {
    margin: 0;
    padding: 0;
}

.file-list li {
    list-style-type: none;
    display: flex;
    height: 200px;
    padding: 20px;
    box-sizing: content-box;
}

.file-list li + li {
    margin-top: 25px;
}

.file-list__preview {
    display: block;
    line-height: 200px;
    flex: 1;
    background-position: cover;
    text-align: center;
    position: relative;
}

.file-list__preview img {
    display: inline-block;
    vertical-align: middle;
    width: auto;
    height: auto;
    max-width: 200px;
    max-height: 200px;
}

.file-list__preview input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    border: none;
    outline: none;
    font-family: monospace;
}

.file-list__textarea {
    flex: 3;
    position: relative;
}

.file-list__textarea textarea {
    font-family: monospace;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    resize: none;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    padding: 0;
}
